*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content{
  height: 120vh;
}
.navbar{
  background-color: rgb(41, 222, 141)!important;
}
/*login*/
.login-container {
  display: flex;
  min-height: 100vh;
  align-items: center;
  background-color: #f4f7fc;
}

.login-left {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-image {
  max-width: 90%;
  height: auto;
}

.login-right {
  flex: 1;
  padding: 40px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0,0,0,0.1);
  margin: 20px;
}

.login-form {
  max-width: 400px;
  margin: auto;
}

/*register*/
.register-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f0f2f5;
  padding: 20px;
  flex-wrap: wrap;
}

.register-left {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  margin: 10px;
}

.register-image {
  max-width: 90%;
  height: auto;
}

.register-right {
  flex: 1;
  min-width: 300px;
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  margin: 10px;
}

.register-form {
  max-width: 400px;
  margin: auto;
}


/*trasection page*/
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}
/*trasection page*/

/*antd icons*/
.anticon svg{
  font-size: 20px;
  cursor: pointer;

}
.active-icon{
  color: gray;
}
.inactive-icon{
  color: red;
}
.icon-container{
  display: flex;
  gap: 2px;
  border: 1px solid 15px rgba(0,0,0,0.6);
  border-radius: 6px;
  padding: 10px 15px;
  box-shadow: 0 0 3px gray;

}
/*antd icons*/
